import React from 'react';
import {NavLink} from 'react-router-dom';

import './../../../assets/scss/style.scss';
import Aux from "../../../hoc/_Aux";
import Breadcrumb from "../../../App/layout/AdminLayout/Breadcrumb";
// import LogoWhite from '../../../assets/images/shapeInlogo_white.svg'
import GDPRWhite from '../../../assets/images/icon_GDPR_compliant_white_full.svg'
import LogoFinancial from '../../../assets/images/logo_financial_times.svg'
import { Redirect } from "react-router-dom";


class SignIn2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          islogged: false,
          loginParams: {
            user_id: "",
            user_password: ""
          }
        };
      }
      handleFormChange = event => {
        let loginParamsNew = { ...this.state.loginParams };
        let val = event.target.value;
        loginParamsNew[event.target.name] = val;
        this.setState({
          loginParams: loginParamsNew
        });
      };

      login = event => {
        let user_id = this.state.loginParams.user_id;
        let user_password = this.state.loginParams.user_password;
        if (user_id === "admin@shapein.com" && user_password === "2rSxGxKH") {
          localStorage.setItem("token", "T");
          this.setState({
            islogged: true
          });
        }
        event.preventDefault();
      };
      render() {
        if (localStorage.getItem("token")) {
          return <Redirect to="/dashboard/default" />;
        }
        return(
            <Aux>
                <Breadcrumb/>
                <div className="auth-wrapper aut-bg-img-side align-items-stretch">
                    <div className="row align-items-center w-100 align-items-stretch bg-white">
                        <div className="d-none d-lg-flex col-lg-8 d-flex justify-content-center" style={{backgroundColor: `#072863`, backgroundSize: 'cover', backgroundAttachment: 'fixed', backgroundPosition: 'center'}}>
                            <div class="row container-fluid align-items-center" >
                                <div class="d-none d-lg-flex col-lg-12 align-self-center">
                                    <img className="client_logo" src={LogoFinancial} alt="LogoFinancial" />
                                </div>
                                <div class="d-none d-lg-flex col-lg-12 align-self-end">
                                    <img className="footer_logo mb-2" src={GDPRWhite} alt="GDPRWhite" />
                                    {/* <img className="footer_logo mb-2" src={LogoWhite} alt="advance-1" /> */}
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 align-items-stret h-100 align-items-center d-flex justify-content-center">
                            <div className=" auth-content text-left">
                                <div className="mb-4">
                                    <i className="feather icon-unlock auth-icon"/>
                                </div>
                                <h1 className="mb-1">Welcome,</h1>
                                <h3 className="mb-4">Sign in to continue</h3>
                                <form onSubmit={this.login} className="form-signin">
                                    <div className="form-group mb-3">
                                        <label for="InputEmail1">Email</label>
                                        {/* <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small> */}
                                        <input id="InputEmail1" type="email" name="user_id" onChange={this.handleFormChange} className="form-control" placeholder="Email"/>
                                    </div>
                                    <div className="form-group mb-4">
                                        <label for="InputPassword">Password</label>
                                        <input id="InputPassword" type="password" name="user_password" className="form-control" onChange={this.handleFormChange} placeholder="password"/>
                                    </div>
                                    <div className="form-group text-left">
                                        <div className="checkbox checkbox-fill d-inline">
                                            <input type="checkbox" name="checkbox-fill-1" id="checkbox-fill-a1"/>
                                                <label htmlFor="checkbox-fill-a1" className="cr"> Save credentials</label>
                                        </div>
                                    </div>

                                    <button className="btn btn-primary shadow-2 mb-4 btn-lg" type="submit" value="Login"><i class="feather icon-check-circle"></i>Login</button>
                                    <p className="mb-2 text-muted">Forgot password? <NavLink to="/auth/reset-password-2">Reset</NavLink></p>
                                    <p className="mb-0 text-muted">Donâ€™t have an account? <NavLink to="/auth/signup-2">Contact your administrators</NavLink></p>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </Aux>
        );
    }
}

export default SignIn2;